html,body {
  font-family: "Iosevka", sans-serif;
  font-weight: lighter;
  -webkit-text-stroke: 1px #acacacaa;
}

body:hover {
  background-blend-mode: initial;
}

.App {
  
  background-color: #191919;
  min-height: 200vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(3vmin);
  color: rgb(231, 231, 231);
  height: 100%;
  
}

.Body {
  min-width: 700px;
  min-height: 200vh;
  flex-direction: column;
  /* align-items: center; */
  /* justify-content: center; */
  width: 50%;
  height: 100%;
  background-color: #2c2c2c;
}

.Header {
  padding-left: 2.5%;
  text-align: left;
  display: flex;
  border-bottom: 2px solid #ababab;
  width: 87.5%;
  margin-left: 5%;
}

.Header > p {
  display: block;
  height: 0.5em;
  text-rendering:geometricPrecision;
  font-size: 1em;
}

.Title {
  width: 100%;
  text-align: center;
  display: block;
}

.Title > p {
  text-align: center;
  margin-top: 0px;
  display: inline;
  -webkit-text-stroke: 0px;
  font-size: 1em;
}

.Title > .Undertext {
  text-align: center;
  display: block;
  opacity: 75%;
  font-size: 0.33em;
}

.Paragraph {
  margin: 0 5% 0 5%;
  padding: 0 0 0% 0;
  font-size: 0.75em;
  margin-bottom: 2.5%;
  text-indent: 7.5%
}

.Date {
  display: inline-block;
  width: 95%;
  font-size: 0.75em;
  height: 0px;
  margin: 0px 0px 0px 0px;
  text-align: right
}

.Code {
  font-family: 'Times New Roman', Times, serif;
  margin: 0 5% 0 5%;
  font-size: 0.75em;
}

.Image {
  margin-top: 5%;
  margin-bottom: 2.5%;
}

.Image > img {
  margin-top: 5%;
  transition: transform 0.25s ease;
  cursor: zoom-in;
  margin-bottom: 2.5%;
}

.ImageEnlarged {
  transform: scale(1.5);
  transition: transform 0.25s ease;
  cursor: zoom-out;
  /* position: absolute; */
  width: 50%;
  height: 50%;
  margin: auto;
}

.Image > * {
  margin: auto;
  max-width: 75%;
  display: block;
  text-align: center;
  margin-bottom: 2.5%;
  font-size: smaller;
}
